import React, { useContext } from "react";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import Slider from "react-slick";
import { MediaContext } from "./store/MediaContext";
const Home = () => {
  let { terdMovie } = useContext(MediaContext);

  var settings = {
    infinite: true,
    slidesToShow: 1,
    slidesToScroll: 1,
    speed: 1000,
    arrows: false,
    autoplay: true,
    autoplaySpeed: 2000,
    focusOnSelect: false,
    pauseOnHover: false,
  };
  return (
    <>
      <div className="   ">
        <Slider className="   " {...settings}>
          {terdMovie &&
            terdMovie.map((movie, index) =>
              index === 0 ? (
                <div
                  key={index}
                  className=" position-relative  p-0 m-0 "
                >
                  <img
                    className="  vh-100  col-md-12 p-0 m-0  img-fluid "
                    src={"https://image.tmdb.org/t/p/w500/" + movie.poster_path}
                    alt="asd"
                  />
                  <p
                    className="   col-md-6  h1  p-0 m-0  "
                  >
                    Lorem ipsum dolor, sit amet consectetur adipisicing elit.
                    Vel, ab beatae deleniti laudantium minima cumque sequi magni
                    doloribus consequatur fugit id suscipit unde,{" "}
                  </p>
                </div>
              ) : (
                terdMovie.map((movie, index) =>
                  index > 0 ? (
                    <div className=" row d-flex p-0 m-0 bg-">
                      <img
                        key={index}
                        className=" test  col-md-4  mt-5 pt-4  "
                        src={
                          "https://image.tmdb.org/t/p/w500/" + movie.poster_path
                        }
                        alt="asd"
                      />
                      <p className=" mt-5 pt-4  col-md-4">
                        Lorem ipsum dolor sit amet, consectetur adipisicing
                        elit. Nihil maxime id at quibusdam sequi voluptatum quam
                        corrupti fugiat assumenda? Sequi, placeat assumenda
                        saepe deserunt animi voluptas cupiditate rerum
                        perferendis nam facilis? 
                      </p>
                    </div>
                  ) : (
                    ""
                  )
                )
              )
            )}
        </Slider>
      </div>
    </>
  );
};

export default Home;
